<template>
    <div id="app">
        <div class="bg" style="position: fixed; left: 0; top: 0; right: 0; bottom: 0;">
            <transition-group name="bgfade" tag="div" class="bg-transition" style="width: 100%; height: 100%;">
                <div v-if="index==imgurlActive" v-for="(imgurl, index) in imgurls" :key="imgurl" class="bg-img" :style="'width: 100%; height: 100%; background-image: url(' + imgurl + ')'"></div>
            </transition-group>
        </div>
        <router-view/>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    export default {
        name: 'App',
        data() {
            return {
                imgurls: [
                    '/static/img/bg/bg1.jpg',
                    '/static/img/bg/bg2.jpg',
                    '/static/img/bg/bg3.jpg',
                    '/static/img/bg/bg4.jpg',
                    '/static/img/bg/bg5.jpg',
                    '/static/img/bg/bg6.jpg',
                    '/static/img/bg/bg7.jpg',
                    '/static/img/bg/bg8.jpg',
                    '/static/img/bg/bg9.jpg',
                    '/static/img/bg/bg10.jpg',
                    '/static/img/bg/bg11.jpg',
                    '/static/img/bg/bg12.jpg',
                    '/static/img/bg/bg13.jpg',
                    '/static/img/bg/bg14.jpg',
                ],
                imgurlActive: 0,
            }
        },
        computed: {
            ...mapGetters([
                'githubUsername',
                'htmlTitle'
            ])
        },
        created() {
            this.$store.dispatch("Init")
            this.$store.dispatch("GetInfo")
            this.$setTitle(this.$route.meta.title)
            let windowSize = this.$util.getWindowSize()
            let pathArr = this.$route.path.split("/")
            if (pathArr[1] == "user" && windowSize.height > windowSize.width * 1.2) {
                this.$router.push("/mobile/user/blog")
            }
            if (pathArr[1] == "mobile" && windowSize.height <= windowSize.width * 1.2) {
                this.$router.push("/")
            }
        },
        mounted() {
            let that = this
            window.setInterval(()=>{
                that.imgurlActive++
                if(that.imgurlActive >= that.imgurls.length){
                    that.imgurlActive = 0
                }
            }, 15000)
            //禁止文本选中
            if (typeof(document.body.onselectstart) != "undefined") {
                // IE下禁止元素被选取
                document.body.onselectstart = new Function("return false");
            } else {
                // firefox下禁止元素被选取的变通办法
                document.body.onmousedown = new Function("return false");
                document.body.onmouseup = new Function("return true");
            } 
        },
    }
</script>

<style lang="less">
body{
    cursor: url(/static/img/default.cur),auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
#app{
    position: absolute;
    left: 0;
    right: 0px;
    top: 0;
    bottom: 0;
    .bg-transition{
        .bgfade-enter-active{
            transition: all 5s;
        }
        .bgfade-leave-active {
            transition: all 2s;
        }
        .bgfade-enter {
            opacity: 0;
        }
        .bgfade-leave-to {
            opacity: 0;
        }
        .bg-img{
            background: 0 0;
            background-attachment: fixed;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
            position: absolute;
            left: 0;
            top: 0;
        }
    }
}
.el-card{//卡片
    border: none;
    background: rgba(255, 255, 255, 0.7);
    .el-card__header{
        border-bottom: 1px dashed #ddd;
    }
    .el-input__inner{
        background-color: rgba(255,255,255,0.7);
    }
    
}
.el-menu{//左中目录
    border: none;
    background-color: rgba(255,255,255,0);
    &.el-menu--horizontal{
        border: none;
    }
}
.el-button.is-circle{//button circle
    background: rgba(255,255,255,0.7);
}
.el-alert--info.is-light{//左下的提示框
    background-color: rgba(255,255,255,0.7);
}
.el-collapse{//使用帮助
    border: none;
    .el-collapse-item__header{
        background-color: rgba(255,255,255,0.7);
    }
    .el-collapse-item__wrap{
        background-color: rgba(255,255,255,0.7);
        border-bottom: 1px solid #eee;
    }
}
</style>



